      getDashboardInfoState: 'account-management/getDashboardInfoState'
    }),
    // 驗證密碼
    vaildPassword(){
      let msg = '' ;
      switch(true){
        case (this.Items.passwordItems.password === ''):
        case (this.Items.passwordItems.passwordConfirmation === ''):
          break;
        default:
          if(this.Items.passwordItems.password !== this.Items.passwordItems.passwordConfirmation)
            msg = this.$ln('密碼 與 確認密碼不同') ;
      }
      return msg ;
    }
  },
