      postSuperAccount: 'api/postSuperAccount',
      getGenerateAccount: 'api/getGenerateAccount',
      getSuperAccountQuota: 'api/getSuperAccountQuota',
      getAppendDefault: 'api/getAppendDefault',
      getUpAccountBingo: 'api/getUpAccountBingo',
      getUpAccountLottery: 'api/getUpAccountLottery',
      getUpAccountRoyal: 'api/getUpAccountRoyal',
      getUpAccount: 'api/getUpAccount'
    }),
    add () {
      let addData = {
        l_no: this.Items.account.selected,
        l_name: this.Items.nameItems.name,
        l_pwd: this.Items.passwordItems.password,
        l_pwd_confirmation: this.Items.passwordItems.passwordConfirmation,
        l_status: this.Items.whetherAllowLogin.selected,
        l_close: this.Items.whetherAllowBet.selected,
        remark: this.Items.remark.contents
      }

      // 上層帳號
      if (this.level < 6 && this.getProfileState.hasOwnProperty('result') && this.getProfileState.result !== undefined) {
        // 新增大股東(level)，若登入帳號(l_lv)不為總監(upLevel)，則需要送上層帳號；若登入帳號(l_lv)為總監(upLevel)，則送登入帳號
        // 新增股東(level)，若登入帳號(l_lv)不為大股東(upLevel)，則需要送上層帳號；若登入帳號(l_lv)為大股東(upLevel)，則送登入帳號
        // 新增總代理(level)，若登入帳號(l_lv)不為股東(upLevel)，則需要送上層帳號；若登入帳號(l_lv)為股東(upLevel)，則送登入帳號
        // 新增代理(level)，若登入帳號(l_lv)不為總代理(upLevel)，則需要送上層帳號；若登入帳號(l_lv)為總代理(upLevel)，則送登入帳號
        // 新增會員(level)，若登入帳號(l_lv)不為代理(upLevel)，則需要送上層帳號；若登入帳號(l_lv)為代理(upLevel)，則送登入帳號
        addData.upAccount = this.getProfileState.result.l_lv !== this.upLevel ? this.upAccount : sessionStorage.getItem('super.manage.account')
      } else if (this.level === 6) {
        // 新增總監(level)，直接送登入帳號 (因為總監，只有在大總監登入才有)
        addData.upAccount = sessionStorage.getItem('super.manage.account')
      }

      // (體育)信用額度
      if (this.Items.sportCreditItems.show && !this.Items.sportCashItems.show) {
        addData.quota_LCtotal = this.Items.sportCreditItems.amount
      // (體育)現金額度
      } else if (!this.Items.sportCreditItems.show && this.Items.sportCashItems.show) {
        addData.quota_LCtotal = this.Items.sportCashItems.amount
      }
      // (BingoStar)信用額度
      if ((this.Items.bingoCreditItems.show && !this.Items.bingoCreditItems.disabled) &&
          !this.Items.bingoCashItems.show &&
          this.Items.bingoCreditItems.noOpenText === '') {
        addData.quota_bingo = this.Items.bingoCreditItems.amount
      // (BingoStar)現金額度
      } else if (!this.Items.bingoCreditItems.show &&
                 (this.Items.bingoCashItems.show && !this.Items.bingoCashItems.disabled) &&
                 this.Items.bingoCashItems.noOpenText === '') {
        addData.quota_bingo = this.Items.bingoCashItems.amount
      }
      // (皇家)信用額度
      if ((this.Items.royalCreditItems.show && !this.Items.royalCreditItems.disabled) &&
          !this.Items.royalCashItems.show &&
          this.Items.royalCreditItems.noOpenText === '') {
        addData.quota_royal = this.Items.royalCreditItems.amount
      // (皇家)現金額度
      } else if (!this.Items.royalCreditItems.show &&
                 (this.Items.royalCashItems.show && !this.Items.royalCashItems.disabled) &&
                 this.Items.royalCashItems.noOpenText === '') {
        addData.quota_royal = this.Items.royalCashItems.amount
      }
      // 單注下限
      if (this.Items.downLimitItems.show) {
        addData.down_limit = this.Items.downLimitItems.amount
      }
      // 開放BingoStar
      if (this.Items.openBingo.show && !this.Items.openBingo.disabled) addData.bingo_status = this.Items.openBingo.selected
      // 開放Super彩球
      if (this.Items.openSuperLottery.show && !this.Items.openSuperLottery.disabled) addData.lottery_status = this.Items.openSuperLottery.selected
      // 開放真人(皇家)類別
      if (this.Items.openRoyal.show && !this.Items.openRoyal.disabled) addData.royal_status = this.Items.openRoyal.selected
      // 拆帳百分比
      let occupyNullCount = 0
      for (let key in this.Items.occupy) {
        if (this.Items.occupy[key].show && !this.Items.occupy[key].disabled) {
          if (key === 'bingo') {
            addData['bingo_occupy'] = this.Items.occupy[key].percentage
          } else if (key === 'royal') {
            addData['royal_occupy'] = this.Items.occupy[key].percentage
          } else {
            addData[key] = this.Items.occupy[key].percentage
          }
          // 拆帳百分比輸入值為空提示通知
          if (this.Items.occupy[key].percentage === '') {
            occupyNullCount++
            this.$toastr.warning(this.$ln(`拆帳百分比「%s」尚未輸入值`, this.Items.occupy[key].title))
          }
        }
      }
      if (occupyNullCount !== 0) return
      try {
        this.postSuperAccount(addData)
          .then(res => {
            if (Object.prototype.toString.call(res) === '[object Error]' &&
                res.hasOwnProperty('response') &&
                res.response.status !== 200) {
              // 錯誤訊息
              if (res.response.data.hasOwnProperty('errors')) common.errorMsgHandler(res.response.data.errors)
              else this.$toastr.error(this.$ln(`新增失敗`))
            } else if (res.status === 200) { // success
              this.$toastr.success(this.$ln(`新增成功`), '', {timeOut: 2000, closeButton: false})
              // 新增成功狀態
              this.$emit('status', true)
              // 關閉彈窗
              this.Items.dialogOpen = false
              if(this.upLevel == sessionStorage.getItem('super.manage.level')){
                this.$router.push(`${this.path}directList`);
                this.$router.push(`${this.path}edit?account=${addData.l_no}`)
              }else{
                this.$router.push(`${this.path}?upAccount=${this.upAccount}`);
                this.$router.push(`${this.path}edit?account=${addData.l_no}&upAccount=${this.upAccount}`)
              }
              // 清除帳號列表記憶頁數 session
              common.clearAccountListSession()
            }
          })
          .catch(err => {
            this.$toastr.error(this.$ln(`新增失敗`))
            console.error(err)
          })
      } catch (error) {
        this.$toastr.error(this.$ln(`站台資料錯誤，請聯絡客服`))
        console.error(error)
      }
    },
    loadGenerateAccount () {
