    // 取得預設資料
    getDefault () {
      // 取得額度預設值
      try {
        let getData = {}
        // 帳號
        // 新增總監，不需要送帳號 (因為總監，只有在大總監登入才有)
        // 新增大股東，若登入帳號(super.manage.level)不為總監(upLevel)，則需要送上層帳號
        // 新增股東，若登入帳號(super.manage.level)不為大股東(upLevel)，則需要送上層帳號
        // 新增總代理，若登入帳號(super.manage.level)不為股東(upLevel)，則需要送上層帳號
        // 新增代理，若登入帳號(super.manage.level)不為總代理(upLevel)，則需要送上層帳號
        // 新增會員，若登入帳號(super.manage.level)不為代理(upLevel)，則需要送上層帳號
        if (parseInt(sessionStorage.getItem('super.manage.level')) !== this.upLevel && this.level < 6) {
          getData.l_no = this.upAccount
        }
        this.getSuperAccountQuota(getData)
          .then(res => {
            if (Object.prototype.toString.call(res) === '[object Error]' &&
                res.hasOwnProperty('response') &&
                res.response.status !== 200) {
              if (res.response.status === 401) return
              // 錯誤訊息
              if (res.response.data.hasOwnProperty('errors')) common.errorMsgHandler(res.response.data.errors)
              else this.$toastr.error(this.$ln(`取得額度預設值失敗`))
              console.error(res.response)
            } else if (res.status === 200) { // success
              // http 200 回應錯誤訊息
              if (res.data.hasOwnProperty('errors')) {
                console.error('response 200 error: ', res.data)
              } else if (res.data.hasOwnProperty('result')) {
                // console.log(`取得帳號 ${this.upAccount} 額度預設值成功`, res.data.result)
                // 設置(體育)剩餘額度值
                if (res.data.result.hasOwnProperty('quota') && res.data.result.quota.hasOwnProperty('sport')) this.Items.sportSurplusQuota = res.data.result.quota.sport.surplus
                // 設置(BingoStar)剩餘額度值
                if (res.data.result.hasOwnProperty('quota') && res.data.result.quota.hasOwnProperty('bingo')) this.Items.bingoSurplusQuota = res.data.result.quota.bingo.surplus
                // 設置(皇家)剩餘額度值
                if (res.data.result.hasOwnProperty('quota') && res.data.result.quota.hasOwnProperty('royal')) this.Items.royalSurplusQuota = res.data.result.quota.royal.surplus
              }
            }
          })
          .catch(err => {
            console.error(err)
          })
      } catch (error) {
        this.$toastr.error(this.$ln(`站台資料錯誤，請聯絡客服`))
        console.error(error)
      }

      // 取得拆帳百分比預設值
      try {
        let getData = {
          balltype: 1
        }
        // 帳號
        // 新增總監，不需要送帳號 (因為總監，只有在大總監登入才有)
        // 新增大股東，若登入帳號(super.manage.level)不為總監(upLevel)，則需要送上層帳號
        // 新增股東，若登入帳號(super.manage.level)不為大股東(upLevel)，則需要送上層帳號
        // 新增總代理，若登入帳號(super.manage.level)不為股東(upLevel)，則需要送上層帳號
        // 新增代理，若登入帳號(super.manage.level)不為總代理(upLevel)，則需要送上層帳號
        // 新增會員，若登入帳號(super.manage.level)不為代理(upLevel)，則需要送上層帳號
        if (parseInt(sessionStorage.getItem('super.manage.level')) !== this.upLevel && this.level < 6) {
          getData.l_no = this.upAccount
        }
        this.getAppendDefault(getData)
          .then(res => {
            if (Object.prototype.toString.call(res) === '[object Error]' &&
                res.hasOwnProperty('response') &&
                res.response.status !== 200) {
              if (res.response.status === 401) return
              // 錯誤訊息
              if (res.response.data.hasOwnProperty('errors')) common.errorMsgHandler(res.response.data.errors)
              else this.$toastr.error(this.$ln(`取得拆帳百分比預設值失敗`))
              console.error(res.response)
            } else if (res.status === 200) { // success
              // http 200 回應錯誤訊息
              if (res.data.hasOwnProperty('errors')) {
                console.error('response 200 error: ', res.data)
              } else if (res.data.hasOwnProperty('result')) {
                // console.log(`取得拆帳百分比預設值成功`, res.data.result)
                // 設置拆帳百分比預設值
                for (let key in res.data.result.occupy) {
                  if (!isNaN(parseInt(key))) {
                    this.Items.occupy[`occupy${key}`].percentage = res.data.result.occupy[key]
                    this.Items.occupy[`occupy${key}`].maxLimit = res.data.result.occupy[key]
                  } else {
                    this.Items.occupy[key].percentage = res.data.result.occupy[key]
                    this.Items.occupy[key].maxLimit = res.data.result.occupy[key]
                  }
                }

                // 設置是否允許登入
                if (res.data.result.hasOwnProperty('l_status')) {
                  this.Items.whetherAllowLogin.selected = res.data.result.l_status
                  this.Items.whetherAllowLogin.disabled = res.data.result.l_status === 1
                } else {
                  this.Items.whetherAllowLogin.disabled = true
                }

                // 設置下線允許投注
                if (res.data.result.hasOwnProperty('l_close')) {
                  this.Items.whetherAllowBet.selected = res.data.result.l_close
                  this.Items.whetherAllowBet.disabled = res.data.result.l_close === 1
                } else {
                  this.Items.whetherAllowBet.disabled = true
                }
              }
            }
          })
          .catch(err => {
            console.error(err)
          })
      } catch (error) {
        this.$toastr.error(this.$ln(`站台資料錯誤，請聯絡客服`))
        console.error(error)
      }
    },
    // 取得[BingoStar]上層帳號資訊
    loadUpAccountBingoStar () {
      // @201909移除賓果@
      return

      try {
        // 新增總監，直接送登入帳號(上層帳號) (因為總監，只有在大總監登入才有)
        // 新增大股東，登入帳號(super.manage.level)為總監(upLevel)，則送登入帳號(上層帳號)，否則送選單帳號(上層帳號)
        // 新增股東，登入帳號(super.manage.level)為大股東(upLevel)，則送登入帳號(上層帳號)，否則送選單帳號(上層帳號)
        // 新增總代理，登入帳號(super.manage.level)為股東(upLevel)，則送登入帳號(上層帳號)，否則送選單帳號(上層帳號)
        // 新增代理，登入帳號(super.manage.level)為總代理，則送登入帳號(上層帳號)，否則送選單帳號(上層帳號)
        // 新增會員，登入帳號(super.manage.level)為代理，則送登入帳號(上層帳號)，否則送選單帳號(上層帳號)
        let account = parseInt(sessionStorage.getItem('super.manage.level')) === this.upLevel || this.level === 6 ? sessionStorage.getItem('super.manage.account') : this.upAccount
        this.getUpAccountBingo({ l_no: account })
          .then(res => {
            if (Object.prototype.toString.call(res) === '[object Error]' &&
                res.hasOwnProperty('response') &&
                res.response.status !== 200) {
              console.error(res.response)
            } else if (res.status === 200) { // success
              // http 200 回應錯誤訊息
              if (res.data.hasOwnProperty('errors')) {
                console.error('response 200 error: ', res.data)
              } else if (res.data.hasOwnProperty('result')) {
                // console.log(`大股東管理-賓果上層(${account})資訊:`, res.data.result)
                // result 為 null 或者狀態為 1 (不開放)
                if (res.data.result &&
                    (res.data.result.hasOwnProperty('status') && res.data.result.status.open_status === 1) ||
                    (res.data.result.hasOwnProperty('hasData') && res.data.result.hasData === 'N')) {
                  // (BingoStar)額度顯示未開放資訊
                  this.Items.bingoCreditItems.noOpenText = '上層未開放'
                  this.Items.bingoCashItems.noOpenText = '上層未開放'
                  // disable 開放BingoStar
                  this.Items.openBingo.disabled = true
                  // disable (BingoStar)拆帳百分比
                  this.Items.occupy.bingo.disabled = true
                } else {
                  // (BingoStar)額度不顯示未開放資訊
                  this.Items.bingoCreditItems.noOpenText = ''
                  this.Items.bingoCashItems.noOpenText = ''
                  // enable 開放BingoStar
                  this.Items.openBingo.disabled = false
                  // enable (BingoStar)拆帳百分比
                  this.Items.occupy.bingo.disabled = false
                }

                // 再依照「開放BingoStar」狀態 disable / enabled (BingoStar)拆帳百分比
                this.Items.occupy.bingo.disabled = this.Items.openBingo.selected === 1
              }
            }
          })
      } catch (error) {
        console.error(error)
      }
    },
