                  // disable (皇家)拆帳百分比
                  this.Items.occupy.royal.disabled = true
                } else {
                  // (皇家)額度不顯示未開放資訊
                  this.Items.royalCreditItems.noOpenText = ''
                  this.Items.royalCashItems.noOpenText = ''
                  // enable 開放真人(皇家)類別
                  this.Items.openRoyal.disabled = false
                  // enable (皇家)拆帳百分比
                  this.Items.occupy.royal.disabled = false
                }

                // 再依照「開放真人(皇家)類別」狀態 disable / enabled (皇家)拆帳百分比
                this.Items.occupy.royal.disabled = this.Items.openRoyal.selected === 1
              }
            }
          })
      } catch (error) {
        console.error(error)
      }
    },
    // 取得上層帳號資訊
    loadUpAccount () {
      try {
        // 新增總監，直接送登入帳號(上層帳號) (因為總監，只有在大總監登入才有)
        // 新增大股東，登入帳號(super.manage.level)為總監(upLevel)，則送登入帳號(上層帳號)，否則送選單帳號(上層帳號)
        // 新增股東，登入帳號(super.manage.level)為大股東(upLevel)，則送登入帳號(上層帳號)，否則送選單帳號(上層帳號)
        // 新增總代理，登入帳號(super.manage.level)為股東(upLevel)，則送登入帳號(上層帳號)，否則送選單帳號(上層帳號)
        // 新增代理，登入帳號(super.manage.level)為總代理，則送登入帳號(上層帳號)，否則送選單帳號(上層帳號)
        // 新增會員，登入帳號(super.manage.level)為代理，則送登入帳號(上層帳號)，否則送選單帳號(上層帳號)
        let account = parseInt(sessionStorage.getItem('super.manage.level')) === this.upLevel || this.level === 6 ? sessionStorage.getItem('super.manage.account') : this.upAccount
        this.getUpAccount({ balltype: 1, l_no: account })
          .then(res => {
            if (Object.prototype.toString.call(res) === '[object Error]' &&
                res.hasOwnProperty('response') &&
                res.response.status !== 200) {
              console.error(res.response)
            } else if (res.status === 200) { // success
              // http 200 回應錯誤訊息
              if (res.data.hasOwnProperty('errors')) {
                console.error('response 200 error: ', res.data)
              } else if (res.data.hasOwnProperty('result')) {
                // console.log(`大股東管理-上層帳號(${account})資訊:`, res.data.result)
                // 設置單注下限
                this.Items.downLimitItems.amount = res.data.result.down_limit
                this.Items.downLimitItems.minLimit = res.data.result.down_limit
              }
            }
          })
          .catch(err => {
            console.error(err)
          })
      } catch (error) {
        this.$toastr.error(this.$ln(`站台資料錯誤，請聯絡客服`))
        console.error(error)
      }
    },
    // 改變所有球類拆帳百分比
    changeAllOccupy () {
      // 輸入值為空值或過濾錯誤不做全部設定
      if (this.Items.setAllPercentageItems.value === '' || !this.Items.setAllPercentageItems.valid) return

      for (let item in this.Items.occupy) {
        this.Items.occupy[item].percentage = this.Items.setAllPercentageItems.value
      }
    },
    // 更新大後台設置資訊
    updateDashboardInfo () {
      let dashboardInfo = JSON.parse(sessionStorage.getItem('super.manage.dashboard.info'))
      if (dashboardInfo &&
          dashboardInfo.hasOwnProperty('wsType') &&
          dashboardInfo.hasOwnProperty('noOpenGame')) {
        /* 註: 先更新站台種類設置再更新開放遊戲設定 */
        // 依照站台種類更新輸入欄(1:現金、2:信用)
        switch (dashboardInfo.wsType) {
          case 1:
            this.Items.sportCashItems.show = true
            this.Items.bingoCashItems.show = true
            this.Items.royalCashItems.show = true
            this.Items.sportCreditItems.show = false
            this.Items.bingoCreditItems.show = false
            this.Items.royalCreditItems.show = false
            break
          case 2:
            this.Items.sportCreditItems.show = true
            this.Items.bingoCreditItems.show = true
            this.Items.royalCreditItems.show = true
            this.Items.sportCashItems.show = false
            this.Items.bingoCashItems.show = false
            this.Items.royalCashItems.show = false
            break
        }
        // 不開放遊戲設定
        for (let i in dashboardInfo.noOpenGame) {
          this.closeGameType(dashboardInfo.noOpenGame[i])
        }

        // 單注下限開放設定 (新增會員必定有單注下限，所以不用判斷大後台設定值，會員以上的層級都需要判斷)
        if (this.level > 1) {
          this.Items.downLimitItems.show = dashboardInfo.showItems.downLimit
        }
      }
    },
    // 不開放遊戲
    closeGameType (type) {
      switch (type) {
        // 關閉世足
        case 's15':
          this.Items.occupy.occupy15.show = false
          break
        // 關閉BingoStar
        case 's16':
          this.Items.bingoCashItems.show = false
          this.Items.bingoCreditItems.show = false
          this.Items.openBingo.show = false
          this.Items.occupy.bingo.show = false
          break
        // 關閉Super彩球
        case 's17':
          this.Items.openSuperLottery.show = false
          break
        // 關閉皇家視訊
        case 's18':
          this.Items.royalCashItems.show = false
          this.Items.royalCreditItems.show = false
          this.Items.openRoyal.show = false
          this.Items.occupy.royal.show = false
          break
      }
    },
    // 初始語系
    initLanguage () {
      // 名稱資料
      this.Items.nameItems.nameRules = [
        v => !!v || this.$ln(`不能為空`),
        v => ((/^[\u4e00-\u9fa5_A-Za-z0-9\(\)\_\-]+$/.test(v)) || v === '') || this.$ln(`不能為特殊符號或空格，除了「括號()」、「底線_」和「-」`),
        v => v.length <= 20 || this.$ln(`不能超過 20 個字元`)
      ]

      // 密碼資料
      this.Items.passwordItems.passwordRules = [
        v => !!v || this.$ln(`不能為空`),
        v => (v.length === 0 || v.length >= 4) || this.$ln(`長度需為 4 位以上`),
        v => ((/^[A-Za-z0-9]+$/.test(v)) || v === '') || this.$ln(`需為英文或數字`),
        v => (!(/^[0-9]+$/.test(v)) || v === '') || this.$ln(`需為英文加數字組合`),
        v => (!(/^[A-Za-z]+$/.test(v)) || v === '') || this.$ln(`需為英文加數字組合`)
      ]

      // (體育)剩餘額度
      this.Items.sportQuotaRules = [
        v => (v !== '') || this.$ln(`不能為空`),
        v => ((/^\d+(\.\d+)*$/.test(v)) || v === '') || this.$ln(`只能數字不能負數和空格`),
        v => (v <= parseFloat(this.Items.sportSurplusQuota)) || this.$ln(`要小於等於 %s`, this.Items.sportSurplusQuota),
        v => (v >= 0) || this.$ln(`不能為負數`)
      ],
      // (BingoStar)剩餘額度
      this.Items.bingoQuotaRules = [
        v => (v !== '') || this.$ln(`不能為空`),
        v => ((/^\d+(\.\d+)*$/.test(v)) || v === '') || this.$ln(`只能數字不能負數和空格`),
        v => (v <= parseFloat(this.Items.bingoSurplusQuota)) || this.$ln(`要小於等於 %s`, this.Items.bingoSurplusQuota),
        v => (v >= 0) || this.$ln(`不能為負數`)
      ],
      // (皇家)剩餘額度
      this.Items.royalQuotaRules = [
        v => (v !== '') || this.$ln(`不能為空`),
        v => ((/^\d+(\.\d+)*$/.test(v)) || v === '') || this.$ln(`只能數字不能負數和空格`),
        v => (v <= parseFloat(this.Items.royalSurplusQuota)) || this.$ln(`要小於等於 %s`, this.Items.royalSurplusQuota),
        v => (v >= 0) || `不能為負數`
      ],

      // 單注下限
      this.Items.downLimitItems.rules = [
        v => (v !== '') || this.$ln(`不能為空`),
        v => ((/^\d+(\.\d+)*$/.test(v)) || v === '') || this.$ln(`只能數字不能負數和空格`),
        v => (v >= parseFloat(this.Items.downLimitItems.minLimit)) || this.$ln(`要大於等於 %s`, this.Items.downLimitItems.minLimit)
      ]

      // 百分比全部設定
      // 過濾規則
      this.Items.setAllPercentageItems.allRules = [
        v => ((/^\d+(\.\d+)*$/.test(v)) || v === '') || this.$ln(`只能數字不能負數和空格`)
      ]
      // 拆帳百分比規則
      // 美棒
      this.Items.occupyRules.occupy1 = [
        v => (v !== '') || this.$ln(`不能為空`),
        v => ((/^\d+(\.\d+)*$/.test(v)) || v === '') || this.$ln(`只能數字不能負數和空格`),
        v => (v <= parseFloat(this.Items.occupy.occupy1.maxLimit)) || this.$ln(`需要 ≤ %s`, this.Items.occupy.occupy1.maxLimit) + '%'
      ]
      // 日棒
      this.Items.occupyRules.occupy2 = [
        v => (v !== '') || this.$ln(`不能為空`),
        v => ((/^\d+(\.\d+)*$/.test(v)) || v === '') || this.$ln(`只能數字不能負數和空格`),
        v => (v <= parseFloat(this.Items.occupy.occupy2.maxLimit)) || this.$ln(`需要 ≤ %s`, this.Items.occupy.occupy2.maxLimit) + '%'
      ]
      // 台棒
      this.Items.occupyRules.occupy3 = [
        v => (v !== '') || this.$ln(`不能為空`),
        v => ((/^\d+(\.\d+)*$/.test(v)) || v === '') || this.$ln(`只能數字不能負數和空格`),
        v => (v <= parseFloat(this.Items.occupy.occupy3.maxLimit)) || this.$ln(`需要 ≤ %s`, this.Items.occupy.occupy3.maxLimit) + '%'
      ]
      // 韓棒
      this.Items.occupyRules.occupy4 = [
        v => (v !== '') || this.$ln(`不能為空`),
        v => ((/^\d+(\.\d+)*$/.test(v)) || v === '') || this.$ln(`只能數字不能負數和空格`),
        v => (v <= parseFloat(this.Items.occupy.occupy4.maxLimit)) || this.$ln(`需要 ≤ %s`, this.Items.occupy.occupy4.maxLimit) + '%'
      ]
      // 冰球
      this.Items.occupyRules.occupy5 = [
        v => (v !== '') || this.$ln(`不能為空`),
        v => ((/^\d+(\.\d+)*$/.test(v)) || v === '') || this.$ln(`只能數字不能負數和空格`),
        v => (v <= parseFloat(this.Items.occupy.occupy5.maxLimit)) || this.$ln(`需要 ≤ %s`, this.Items.occupy.occupy5.maxLimit) + '%'
      ]
      // 籃球
      this.Items.occupyRules.occupy6 = [
        v => (v !== '') || this.$ln(`不能為空`),
        v => ((/^\d+(\.\d+)*$/.test(v)) || v === '') || this.$ln(`只能數字不能負數和空格`),
        v => (v <= parseFloat(this.Items.occupy.occupy6.maxLimit)) || this.$ln(`需要 ≤ %s`, this.Items.occupy.occupy6.maxLimit) + '%'
      ]
      // 彩球
      this.Items.occupyRules.occupy7 = [
        v => (v !== '') || this.$ln(`不能為空`),
        v => ((/^\d+(\.\d+)*$/.test(v)) || v === '') || this.$ln(`只能數字不能負數和空格`),
        v => (v <= parseFloat(this.Items.occupy.occupy7.maxLimit)) || this.$ln(`需要 ≤ %s`, this.Items.occupy.occupy7.maxLimit) + '%'
      ]
      // 美足
      this.Items.occupyRules.occupy8 = [
        v => (v !== '') || this.$ln(`不能為空`),
        v => ((/^\d+(\.\d+)*$/.test(v)) || v === '') || this.$ln(`只能數字不能負數和空格`),
        v => (v <= parseFloat(this.Items.occupy.occupy8.maxLimit)) || this.$ln(`需要 ≤ %s`, this.Items.occupy.occupy8.maxLimit) + '%'
      ]
      // 網球
      this.Items.occupyRules.occupy9 = [
        v => (v !== '') || this.$ln(`不能為空`),
        v => ((/^\d+(\.\d+)*$/.test(v)) || v === '') || this.$ln(`只能數字不能負數和空格`),
        v => (v <= parseFloat(this.Items.occupy.occupy9.maxLimit)) || this.$ln(`需要 ≤ %s`, this.Items.occupy.occupy9.maxLimit) + '%'
      ]
      // 足球
      this.Items.occupyRules.occupy10 = [
        v => (v !== '') || this.$ln(`不能為空`),
        v => ((/^\d+(\.\d+)*$/.test(v)) || v === '') || this.$ln(`只能數字不能負數和空格`),
        v => (v <= parseFloat(this.Items.occupy.occupy10.maxLimit)) || this.$ln(`需要 ≤ %s`, this.Items.occupy.occupy10.maxLimit) + '%'
      ]
      // 指數
      this.Items.occupyRules.occupy11 = [
        v => (v !== '') || this.$ln(`不能為空`),
        v => ((/^\d+(\.\d+)*$/.test(v)) || v === '') || this.$ln(`只能數字不能負數和空格`),
        v => (v <= parseFloat(this.Items.occupy.occupy11.maxLimit)) || this.$ln(`需要 ≤ %s`, this.Items.occupy.occupy11.maxLimit) + '%'
      ]
      // 賽馬＆賽狗
      this.Items.occupyRules.occupy12 = [
        v => (v !== '') || this.$ln(`不能為空`),
        v => ((/^\d+(\.\d+)*$/.test(v)) || v === '') || this.$ln(`只能數字不能負數和空格`),
        v => (v <= parseFloat(this.Items.occupy.occupy12.maxLimit)) || this.$ln(`需要 ≤ %s`, this.Items.occupy.occupy12.maxLimit) + '%'
      ]
      // 電競
      this.Items.occupyRules.occupy13 = [
        v => (v !== '') || this.$ln(`不能為空`),
        v => ((/^\d+(\.\d+)*$/.test(v)) || v === '') || this.$ln(`只能數字不能負數和空格`),
        v => (v <= parseFloat(this.Items.occupy.occupy13.maxLimit)) || this.$ln(`需要 ≤ %s`, this.Items.occupy.occupy13.maxLimit) + '%'
      ]
      // 其他
      this.Items.occupyRules.occupy14 = [
        v => (v !== '') || this.$ln(`不能為空`),
        v => ((/^\d+(\.\d+)*$/.test(v)) || v === '') || this.$ln(`只能數字不能負數和空格`),
        v => (v <= parseFloat(this.Items.occupy.occupy14.maxLimit)) || this.$ln(`需要 ≤ %s`, this.Items.occupy.occupy14.maxLimit) + '%'
      ]
      // 世足
      this.Items.occupyRules.occupy15 = [
        v => (v !== '') || this.$ln(`不能為空`),
        v => ((/^\d+(\.\d+)*$/.test(v)) || v === '') || this.$ln(`只能數字不能負數和空格`),
        v => (v <= parseFloat(this.Items.occupy.occupy15.maxLimit)) || this.$ln(`需要 ≤ %s`, this.Items.occupy.occupy15.maxLimit) + '%'
      ]
      // BingoStar
      this.Items.occupyRules.bingo = [
        v => (v !== '') || this.$ln(`不能為空`),
        v => ((/^\d+(\.\d+)*$/.test(v)) || v === '') || this.$ln(`只能數字不能負數和空格`),
        v => (v <= parseFloat(this.Items.occupy.bingo.maxLimit)) || this.$ln(`需要 ≤ %s`, this.Items.occupy.bingo.maxLimit) + '%'
      ]
      // 皇家
      this.Items.occupyRules.royal = [
        v => (v !== '') || this.$ln(`不能為空`),
        v => ((/^\d+(\.\d+)*$/.test(v)) || v === '') || this.$ln(`只能數字不能負數和空格`),
        v => (v <= parseFloat(this.Items.occupy.royal.maxLimit)) || this.$ln(`需要 ≤ %s`, this.Items.occupy.royal.maxLimit) + '%'
      ]

      // (體育)信用額度 [信用 / 剩餘額度]
      this.Items.sportCreditItems.suffixLeft = `(${this.$ln('萬')}) / `
      this.Items.sportCreditItems.suffixRight = ` (${this.$ln('萬')})`

      // (BingoStar)信用額度 [信用 / 剩餘額度]
      this.Items.bingoCreditItems.suffixLeft = `(${this.$ln('萬')}) / `
      this.Items.bingoCreditItems.suffixRight = ` (${this.$ln('萬')})`
      // 勾選提示
      this.Items.bingoCreditItems.promptText = this.$ln('點選「開放BingoStar」為(是)時設定')

      // (皇家)信用額度 [信用 / 剩餘額度]
      this.Items.royalCreditItems.suffixLeft = `(${this.$ln('萬')}) / `
      this.Items.royalCreditItems.suffixRight = ` (${this.$ln('萬')})`
      // 勾選提示
      this.Items.royalCreditItems.promptText = this.$ln('點選「開放真人(皇家)類別」為(是)時設定')

      // (體育)現金額度 [現金 <= 剩餘額度]
      this.Items.sportCashItems.suffixLeft = `${this.$ln(sessionStorage.getItem('super.manage.dashboard.info.unit2'))}  <= `
      this.Items.sportCashItems.suffixRight = ` ${this.$ln(sessionStorage.getItem('super.manage.dashboard.info.unit2'))}`

      // (BingoStar)現金額度 [現金 <= 剩餘額度]
      this.Items.bingoCashItems.suffixLeft = `${this.$ln(sessionStorage.getItem('super.manage.dashboard.info.unit2'))}  <= `
      this.Items.bingoCashItems.suffixRight = ` ${this.$ln(sessionStorage.getItem('super.manage.dashboard.info.unit2'))}`
      // 勾選提示
      this.Items.bingoCashItems.promptText = this.$ln('點選「開放BingoStar」為(是)時設定')

      // (皇家)現金額度 [現金 <= 剩餘額度]
      this.Items.royalCashItems.suffixLeft = `${this.$ln(sessionStorage.getItem('super.manage.dashboard.info.unit2'))}  <= `
      this.Items.royalCashItems.suffixRight = ` ${this.$ln(sessionStorage.getItem('super.manage.dashboard.info.unit2'))}`
      // 勾選提示
      this.Items.royalCashItems.promptText = this.$ln('點選「開放真人(皇家)類別」為(是)時設定')
    }
  },
  //BEGIN--生命週期
  beforeCreate: function() {
    //實體初始化
  },
  created: function() {
    //實體建立完成。資料 $data 已可取得，但 $el 屬性還未被建立。
    // this.src = this.$options.__file ;
  },
  beforeMount: function() {
    //執行元素掛載之前。
  },
  mounted: function() {
    //元素已掛載， $el 被建立。
    this.initLanguage()
  },
  beforeUpdate: function() {
    //當資料變化時被呼叫，還不會描繪 View。
  },
  updated: function() {
    //當資料變化時被呼叫，還不會描繪 View。
  },
  beforeDestroy: function() {
    //實體還可使用。
  },
  destroyed: function() {
    //實體銷毀。
  }
  //END--生命週期
};
</script>

